            <form action="" v-on:submit.prevent="addExcercise">
              <div class="row align-items-*">
                <div class="col-sm">
                  <span>Nombre</span>
                  <input
                    type="text"
                    name=""
                    id=""
                    class="form-control"
                    v-model="json.name"
                  />
                </div>
              </div>
              <div class="row align-items-*">
                <div class="col-sm">
                  <span>Descripcin</span>
                  <input
                    type="text"
                    name=""
                    id=""
                    class="form-control"
                    v-model="json.description"
                  />
                </div>
              </div>
              <div class="row align-items-*">
                <div class="col-sm-12">
                  <span>URL</span>
                  <input
                    type="url"
                    name=""
