<template>
  <div>
    <Header />
    <br><br>
    <div class="container">
      <br />
      <div class="imgEjercicio"></div>
      <br />
      <br />
      <div class="title text-center">
        <h1 id="Ejericios">Ejercicios</h1>
      </div>
      <br />
      <div class="blog-card" v-for="rutina in rutinas" :key="rutina.id">
        <div class="meta">
          <div class="photo">
            <img
              :src="rutina.url_exercise"
              :title="rutina.name_exercise"
              style="width: 300px; height: 100%"
              alt=""
            />
          </div>
        </div>
        <div class="description">
          <h1>{{ rutina.name_exercise }}</h1>
          <p>{{ rutina.descripcion_exercise }}</p>
          <p class="read-more">
            series {{ rutina.series }} y repeticiones {{ rutina.repetitions }}
          </p>
        </div>
      </div>
      <br />
      <div class="areabtn text-center">
        <form v-on:submit.prevent="aunmentarXP">
        <button class="btn btn-success">
          Terminar
        </button>
      </form>
      </div>
      <br />
      <br />
    </div>
    <Footer />
  </div>
</template>

<script>
import axios from "axios";
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "RutinaEjercicioView",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      error: false,
      rutinas: null,
      calorias_quemadas: null,
      error_msg: "",
      URL: process.env.VUE_APP_API_HOST,
    };
  },
  async mounted() {
    if (localStorage.token) {
      console.log("existe");
    } else {
      localStorage.token = "";
    }
    if (localStorage.token == "" || !localStorage.token) {
      console.log("no logeado");
      this.$router.push("/");
    }
    this.getRutines();
  },
  methods: {
    getRutines() {
      this.rutinas = JSON.parse(localStorage.getItem("Rutinas"));
      this.calorias_quemadas = JSON.parse(localStorage.getItem("Calorias"));
      console.log(this.rutinas);
      console.log(this.calorias_quemadas);
    },
    aunmentarXP() {
      axios
        .put(process.env.VUE_APP_API_HOST + "usr/level_up",{caloria:this.calorias_quemadas},{
          headers: {
            Authorization: `Bearer ${localStorage.token}`,
          },
        })
        .then((response) => {
          console.log(response);
          axios
            .post(process.env.VUE_APP_API_HOST + "calorias/add_calories",{caloria:this.calorias_quemadas}, {
              headers: {
                Authorization: `Bearer ${localStorage.token}`,
              },
            })
            .then((response) => {
              console.log(response);
              this.goToPage("/congratulations");
            })
            .catch((e) => console.log(e));
        })
        .catch((e) => console.log(e));
    },
    goToPage(page) {
      this.$router.push(page);
    },
  },
};
</script>

